import { Component, OnDestroy, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Subscription } from 'rxjs';
import { LoadingSpinnerService } from '../../../services/spinner/loading-spinner.service';

@Component({
  selector: 'app-loading-spinner',
  templateUrl: './loading-spinner.component.html',
  styleUrls: ['./loading-spinner.component.scss']
})
export class LoadingSpinnerComponent implements OnInit, OnDestroy {

  loadingSubjection: Subscription | undefined;
  isLoading = false;
  constructor(private loaderService: LoadingSpinnerService, private router: Router) { }

   ngOnInit():void {
    this.loadingSubjection = this.loaderService.isLoading.subscribe( (res) => {
      this.isLoading = res;
    });
  }




  ngOnDestroy(): void {
    if (this.loadingSubjection && !this.loadingSubjection.closed) {
      this.loadingSubjection.unsubscribe();
    }
  }

}
